
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Button, Form, FormCheck, Stack, Toast } from 'react-bootstrap';
import { useState } from 'react';
import axios from 'axios';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { useEffect } from 'react';
import { useRef } from 'react';
function App() {
  let [companyName,setCompanyName]=useState("")
  let [csvFiles,setCSVFiles]=useState("")
  let [excelFiles,setExcelFiles]=useState("")
  let [data,setData]=useState([])
  let [selectData,setSelectedData]=useState(data)
  let [switchState,setswitch]=useState(true)
  let get=()=>{
    axios.get("http://localhost:7000/getCompanies")
    .then(function (response) {
      setData(response.data.data||[])
      if(switchState){
       setSelectedData(response.data.data||[])
      }
    })
    .catch(function (error) {
      console.log(error);
    });
    
  }
  useEffect(()=>{
   get()
  },[])
  return (
  <div className='app'>
    <h4 className='title'>AGS CSV To EXCEL Convertor</h4>
   <div className='companyForm'>
   <Form  >
      <fieldset >
        <Form.Group className="mb-3">
          <Form.Label htmlFor="disabledTextInput">Enter Company Name</Form.Label>
          <Form.Control 
           value={companyName}
           onChange={e => {
            setCompanyName( e.target.value)
           }}
          id="disabledTextInput" placeholder="company name" />
         <Form.Label>CSV Folder path</Form.Label>
         <Form.Control
          value={csvFiles}
          onChange={e => {
           setCSVFiles( e.target.value)
          }}
         id="disabledTextInput" placeholder="import csv files path" />
         <Form.Label>EXCEL Files Folder path</Form.Label>
         <Form.Control 
          value={excelFiles}
          onChange={e => {
           setExcelFiles(e.target.value)
          }}
         id="disabledTextInput" placeholder="export excel files path" />
        </Form.Group>
        <Button onClick={(e)=>{
            
             if(companyName.length==0 ||csvFiles.length==0 ||
               excelFiles.length==0){
                toast.info(' Please fill all fields', {
                  position: "top-center",
                  autoClose: 1000,
                  hideProgressBar: false,
                  closeOnClick: true,
                  pauseOnHover: true,
                  draggable: true,
                  progress: undefined,
                  theme: "light",
                  });
                return
               }
             var data = {
               "name":companyName,
               "csv": csvFiles,
               "excel":excelFiles
             };
             
             axios.post('http://localhost:7000/create',data)
             .then(function (response) {
              toast.success(' Company Created', {
                position: "top-center",
                autoClose: 1000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
                });
                get()
             })
             .catch(function (error) {
              toast.error(' Error Occurred', {
                position: "top-center",
                autoClose: 1000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
                });
             });
               
        }}>Submit</Button>
      </fieldset>
    </Form>
   
    
   </div>
   <h4 className='title-c'>Companies List</h4>
    <div className='list'>
    {
      data.map((item,index)=>{
       
        let select=selectData?.find(ele=>ele.name==item.name)
        
        return <div key={index} className='companyitem'>
          <p>
          {item.name}
          <br></br>
          CSV path ----- {item.csv}
          <br></br>
          Excel path ----- {item.excel}
          </p>
          <p className='remove'
          onClick={()=>{
            if(!switchState){
               if(select){
                  let filter=selectData.filter(it=>it.name!=select.name)
                  console.log(filter)
                  setSelectedData(filter)
               }else{
                  let temp=selectData
                  temp.push(item)
                  console.log(temp)
                  select=true
                  setSelectedData(temp)  
               }
            }
          }}
          style={{backgroundColor:select?'green':"",color:select?'white':""}}>
            Select company
          </p>
          <p className='remove' onClick={()=>{
           
          
          axios.get('http://localhost:7000/remove?index='+index)
          .then(function (response) {
            console.log(JSON.stringify(response.data));
            get()
          })
          .catch(function (error) {
            console.log(error);
          });
          
          }}>
            remove company
          </p>
        </div>
      })

    }
      
    
    </div>
   {
    data.length!=0 &&  <div className="bt">
    <Form>
      <Form.Check 
        type="switch"
        checked={switchState}
        onChange={(e)=>{
           
           if(!switchState){
            setswitch(true)
            setSelectedData(data)
           }else{
            setswitch(false)
            setSelectedData([])
           }
        }}
        id="custom-switch"
        label="Select All" 
      />
      
    </Form>
    <Button className="bt" onClick={()=>{
       
      
      toast.promise(
        axios.post('http://localhost:7000/convert',{
        data:selectData
      }),
        {
          pending: 'Converting please wait',
          success: 'Conversion done  ',
          error: ' rejected '
        })
      }
    } >Convert</Button>
    </div>
   }
    
   <ToastContainer
position="top-center"
autoClose={5000}
hideProgressBar={false}
newestOnTop={false}
closeOnClick
rtl={false}
pauseOnFocusLoss
draggable
pauseOnHover
theme="light"
/>

</div>

  );
}

export default App;
